import React from 'react'
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import 'bootstrap/dist/css/bootstrap.min.css';
import Typical from 'react-typical'
import monogram_logo from './assets/img/monogram_icon.png'
import './App.css'

function App() {
  return (
    <div>
      <Navbar expand="lg" sticky="top">
          <Navbar.Brand href="#home">
            <img src={monogram_logo}/>
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="mr-auto" className="NavText">
              <Nav.Link href="#home">Home</Nav.Link>
              <Nav.Link href="#link">Link</Nav.Link>
            </Nav>
          </Navbar.Collapse>
      </Navbar>
      <h1 className="TeusdayHeader">
        What we do?
      </h1>
      <p className="App">
        <Typical
          loop={Infinity}
          wrapper="p"
          steps={['Graphic Design ', 1500,
                  'Android Developement ', 1500,
                  'Website Developement ', 1500
              ]}
        />
      </p>
    </div>
  );
}

export default App;